import IndexModel from '@/models/index'
import { checkMobile } from '@/util/utils'
export default {
	data() {
		return {
			title: 'Hello1',
			list: [],
			form: {
				num1: '',
				num2: '',
				tel: ''
			},
			dialogFlag: false
		}
	},
	async onLoad() {
		this.list = await IndexModel._getData()
	},
	methods: {
		onSubmit() {
			
			const duration = 2000
			if (!this.form.num1) {
				uni.showToast({
				    title: '请输入工频空压机台数',
					icon: 'none',
				    duration
				})
				return
			}
			if (!this.form.num2) {
				uni.showToast({
				    title: '请输入工频空压机台数',
					icon: 'none',
				    duration
				})
				return
			}
			if (!checkMobile(this.form.tel)) {
				uni.showToast({
				    title: '请输入正确的手机号',
					icon: 'none',
				    duration
				})
				return
			}
			this.onShowDialog()
		},
		onShowDialog() {
			this.dialogFlag = true
		},
		onHideDialog() {
			this.dialogFlag = false
		},
		onGoToWebView() {
			uni.navigateTo({
			    url: `/pages/webView/webView?src=${encodeURIComponent('https://mp.weixin.qq.com/s/xU8FOaGlF0tqK_b5VGV1iQ')}`
			    // url: `/pages/webView/webView?src=${encodeURIComponent('https://baidu.com')}`
			});
		},
		onCallTel() {
			uni.makePhoneCall({
			    phoneNumber: '400-086-2020' //仅为示例
			});
		}
	}
}
